<script>
import { TimelineLite } from "gsap";
import { SceneNames } from '../../../../global/constants';
import { Howl } from 'howler';

export default {
    name: "Opening",
    mounted() {

        if(this.$store.state.session.sound) {
            var sound = new Howl({
                src: require("../../../../assets/sounds/opening.mp3"),
                volume: 1,
                loop: true
            });

            sound.play();
        }

        var timeLine = new TimelineLite({
            paused: true,
            
        });

        timeLine.to('#opening_sprite_1', {display: 'none', duration: 1,  ease: "none"});
        timeLine.to('#opening_sprite_2', {display: 'block', duration: 1,  ease: "none"});
        timeLine.set('#opening_sprite_2', {display: 'none'});
        timeLine.to('#opening_sprite_3', {display: 'block', duration: 1,  ease: "none"});
        timeLine.set('#opening_sprite_3', {display: 'none'});
        timeLine.to('#opening_sprite_4', {display: 'block', duration: 1,  ease: "none"});
        timeLine.set('#opening_sprite_4', {display: 'none'});
        timeLine.to('#opening_sprite_5', {display: 'block', duration: 1,  ease: "none"});
        timeLine.set('#opening_sprite_5', {display: 'none'});
        timeLine.to('#opening_sprite_6', {display: 'block', duration: 1,  ease: "none"});

        timeLine.play().then(() => {
            sound.stop();
            this.moveToStandByScene()
        });
    },
    methods: {   
        moveToStandByScene() {
            this.$store.commit('session/setCurrentScene', SceneNames.StandBy);
        }
    }        
}
</script>

